export default {

	props: {
		item: Object,
		index: Number
	},

	data() {
		return {
			isGuanZhu: this.item.isGuanZhu,
			infonum: this.item.infonum
		}
	},
	methods: {

		guanzhu() {

			this.isGuanZhu = true;
			uni.showToast({ //弹框
				title: '关注成功',
				icon: 'success'
			})
		},
		doding() {
			//未顶--置黄 +1 （如果踩了 踩置默认 -1）
			//已经顶 -- 置默认 -1
			console.log("======this.info.index==" + this.infonum.index);
			if (this.infonum.index == 0) {
				this.infonum.ding++;
				this.infonum.index = 1;
			} else if (this.infonum.index == 1) {
				this.infonum.ding--;
				this.infonum.index = 0;
			} else if (this.infonum.index == 2) {
				this.infonum.cai--;
				this.infonum.ding++;
				this.infonum.index = 1;
			}
		},
		docai() {
			console.log("======this.info.index==" + this.infonum.index);
			if (this.infonum.index == 0) {
				this.infonum.cai++;
				this.infonum.index = 2;
			} else if (this.infonum.index == 2) {
				this.infonum.cai--;
				this.infonum.index = 0;
			} else if (this.infonum.index == 1) {
				this.infonum.ding--;
				this.infonum.cai++;
				this.infonum.index = 2;
			}
		},
		caoZuo(type) { //点击顶和踩的操作
			switch (type) {
				case 'ding':
					if (this.infonum.index == 1) { //只执行一次
						return false;
					}
					this.infonum.ding++;
					if (this.infonum.index == 2) { //只执行一次
						this.infonum.cai--;
					}
					this.infonum.index = 1;
					break;
				case 'cai':
					if (this.infonum.index == 2) { //只执行一次
						return false;
					}
					this.infonum.cai++;
					if (this.infonum.index == 1) { //只执行一次
						this.infonum.ding--;
					}
					this.infonum.index = 2;
					break;
				default:
					break;
			}
		},
		gotodetail(){
			uni.showToast({
				title: '进入详情页'
			});
		}
	}
}
